import React, { useState } from 'react'
import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Form,Button,Container} from 'react-bootstrap'
export default function Create(){
    const usersData = [     
        { id: 1, name: 'Tania' },
        { id: 2, name: 'Craig' },
        { id: 3, name: 'Ben' }
       ]
       const[users,setUsers]=useState(usersData);
       //delete functionality
      const deleteUser = id => {
       setUsers(users.filter(user => user.id !== id))
      }
      //  add user state
      const[name,setName]=useState("");
      // handleChange on name
     const handleChange=(e)=>{
         setName(e.target.value)
     }
     // add entry functionality
     const handleSubmit=(e)=>{
         e.preventDefault()
         setUsers([
             ...users,
             {
                 id:users.length+1,
                 name:name
             }
         ])
          setName(" ")
     }
    return(
        <div>
            <Container>
            < Form>
                <input type="text" placeholder="enter name here" name="name" value={name} onChange={handleChange}/>
                <Button onClick={handleSubmit}>submit</Button>
            </Form><br/>
              {
                  users.map(function(item){
                      return(
                           <div key={item.id} className="entry" ><h1 className="me-5">{item.name}</h1>   <Button variant="danger"   onClick={()=>deleteUser(item.id)} >Delete</Button>
                               </div>
                      )
                  })
              }
              </Container>
        </div>
    )
}